
import Vue from 'vue'
import Router from 'vue-router'

import UserLogin from './../components/UserLogin.vue' //登陆页面
import UserRegister from './../components/UserRegister.vue' //注册页面
import ForgetPassword from './../components/ForgetPassword.vue' //忘记密码页面
import UserCenter from './../components/UserCenter.vue' //我的页面
import Home from './../components/Home.vue' //主页页面
import GroupList from './../components/GroupList.vue' //投注页面
import GroupListTw from './../components/GroupListTw.vue' //投注完成页面
import ShoppingList from './../components/ShoppingList.vue' //玩法页面
import UserEmbody from './../components/UserEmbody.vue' //提现页面
import UserEmbodyList from './../components/UserEmbodyList.vue' //提现列表页面
import UserRecharge from './../components/UserRecharge.vue' //充值页面
import AboutIntroduce from './../components/AboutIntroduce.vue' //关于我们页面
import CustomService from './../components/CustomService.vue' //客服页面
import PersonalInformation from './../components/PersonalInformation.vue' //客服页面
import UpdatePwd from './../components/UpdatePwd.vue' //客服页面
import chongzhi from './../components/chongzhi.vue' //充值列表页面
import zhongjiang from './../components/zhongjiang.vue' //中奖记录列表页面
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        title: '温州快8-首页',
      }

    },
    {
      path: "/UpdatePwd",
      name: "UpdatePwd",
      component: UpdatePwd,
      meta: {
        title: "温州快8-修改密码"
      }
    },
    {
      path: '/UserRegister',
      name: 'UserRegister',
      component: UserRegister,
      meta: {
        title: '温州快8-用户注册',
      }

    },
    {
      path: '/UserLogin',
      name: 'UserLogin',
      component: UserLogin,
      meta: {
        title: '温州快8-用户登录'
      }
    },
    {
      path: '/ForgetPassword',
      name: 'ForgetPassword',
      component: ForgetPassword,
      meta: {
        title: '温州快8-忘记密码',

      }

    },
    {
      path: '/UserCenter',
      name: 'UserCenter',
      component: UserCenter,
      meta: {
        title: '温州快8-用户中心',
        requireAuth: true,
      }

    },
    {
      path: '/GroupList',
      name: 'GroupList',
      component: GroupList,
      meta: {
        title: '温州快8-投注',
      }

    },
    {
      path: '/GroupListTw',
      name: 'GroupListTw',
      component: GroupListTw,
      meta: {
        title: '温州快8-投注完成',
      }

    },
    {
      path: '/ShoppingList',
      name: 'ShoppingList',
      component: ShoppingList,
      meta: {
        title: '温州快8-玩法',
      }

    },
    {
      path: '/UserEmbody',
      name: 'UserEmbody',
      component: UserEmbody,
      meta: {
        title: '温州快8-提现',
      }

    },
    {
      path: '/UserEmbodyList',
      name: 'UserEmbodyList',
      component: UserEmbodyList,
      meta: {
        title: '温州快8-提现列表',
      }

    },
    {
      path: '/PersonalInformation',
      name: 'PersonalInformation',
      component: PersonalInformation,
      meta: {
        title: '温州快8-个人信息',
      }

    },
    {
      path: '/UserRecharge',
      name: 'UserRecharge',
      component: UserRecharge,
      meta: {
        title: '温州快8-充值',
      }

    },
    {
      path: '/AboutIntroduce',
      name: 'AboutIntroduce',
      component: AboutIntroduce,
      meta: {
        title: '温州快8-关于我们',
      }

    },
    {
      path: '/CustomService',
      name: 'CustomService',
      component: CustomService,
      meta: {
        title: '温州快8-客服',
      }

    },
    {
      path: '/chongzhi',
      name: 'chongzhi',
      component: chongzhi,
      meta: {
        title: '温州快8-充值',
      }

    },
    {
      path: '/zhongjiang',
      name: 'zhongjiang',
      component: zhongjiang,
      meta: {
        title: '温州快8-中奖列表',
        requireAuth: true,
      }

    },
  ],
  mode: "history"//干掉地址栏里边的#号键
})




