<template>
    <div id="UserEmbody">
        <mt-header title="提现" id="embodyHeader">
            <router-link to="/UserEmbodyList" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="embodyTop" action="">
            <mt-field label="金额" placeholder="请输入提现金额" type="number" v-model="money"></mt-field>
        </div>
        <div class="userMony">可提现余额<span>¥{{usermy}}</span></div>
        <div class="embodyBtn"><mt-button type="danger" class="bodyBtn" @click="endgBtn">下一步</mt-button></div>
        <div  class="payBox" v-if='showPay'>
            <el-row>
              <el-col :span="18" class="login-el-col" style="position: relative;left: 50%;margin-left: -37%;margin-top: 35px;">
                <el-input  v-model="payPassword" id="UserPhone" placeholder="请输入支付密码" class="el-input"></el-input>
              </el-col>
            </el-row>
          <div class="embodyBtn" style="border-top: 0;"><mt-button type="danger" class="bodyBtn" @click='checkP'>下一步</mt-button></div>
        </div>

    </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "UserEmbody",
  data(){
    return{
      money:"", //体现金额
      usermy:0, //可用体现金额
      bankflag:0,
      payPassword:'',
      showPay:false
    }

  },
  created(){
        console.log(this.$store.state.userInfo)
      this.usermy=this.$store.state.userInfo.balance
    },
  methods: {
    checkP:function(){
            var money =  this.money;
            axios.post("/api/apply/userApply",{
                money:money,
                payPassword:this.payPassword
            }).then((data)=>{
                if (data.data.code === 10000) {
                  alert("提现成功")
                  this.showPay=false;
                  this.$router.push('/UserEmbodyList');
                } else {
                  alert(data.data.msg)
                  this.showPay=false;
                  //this.$router.push('/UserEmbodyList');
                }

            })
    },
    endgBtn:function(){
        var money =  this.money;
        if(money == ""){
            this.tipTxt="请输入提现金额";
            this.tipBox();
            this.bankflag=0;
            return false;
        }else if(money > this.usermy){
            this.tipTxt="提现金额大于余额";
            this.tipBox();
            this.bankflag=0;
            return false;
        }
        this.showPay=true


    },
    tipBox:function(){
        alert(this.tipTxt);
        //执行封装的报错弹窗提醒，错误提示文本为this.tipTxt
    },


  }
}
</script>

<style>
*{font-size: 0.25rem;}
#embodyHeader{color: #2d2d2d;background: #fff;}
#UserEmbody{width: 100%; height: 100%; position: fixed;background: #f6f6f6;}
.mint-field .mint-cell-title{width: 50px}
.embodyTop{display:block; margin-top: 20px;}
.mint-cell-wrapper {font-size: 0.25rem;background-image: -webkit-gradient(linear, left top, left bottom, from(#f6f6f6), color-stop(50%, #f6f6f6), color-stop(50%, transparent));background-image: linear-gradient(180deg, #f6f6f6, #f6f6f6 50%, transparent 50%);padding: 0 0px;width: 100%; text-indent: 10px;}
.mint-cell:nth-child(4){margin-top: 10px;}
.userMony{width: 100%;height: 48px;background: #fdfdfd;line-height: 48px;text-indent: 10px;color: #717171;}
.userMony span{padding-left: 0.2rem;}
.embodyBtn{border-top:4px solid #f6f6f6; margin-top: 0.4rem;}
.bodyBtn{width: 60%; margin-left: 20%;}
.bankList{width: 100%;height: 48px;border: 0;text-indent: 10px;outline: none;}
.mintui{margin-right: 0.2rem;}
.payBox{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.8);
    z-index: 9999999999999999999;
}
</style>



