<template>
    <div id="ShoppingList">
        <!-- 头部 -->
        <mt-header fixed title="玩法规则" id="playheight"></mt-header>
        <div class="playRule">
          <h4>价格说明：</h4>
          <p>1：每注彩票价格为人民币10元。多倍价格往上翻，如一个号码买10倍，即100元。</p>
          <h4>玩法说明：</h4>
          <p>1：每10分钟一次，前九分半为选号时间，后半分钟为封存数据开奖时间。一天24小时随时购买。</p>
          <p>2：从0-9三位数中选择一个号码，总共3组号码：如123等等</p>
          <p>3：一组号码最高投注背数为99倍，中奖也是中奖奖金的99倍。如本次中奖100元，投注99倍即总奖金为9900元</p>
          <h4>奖金说明：</h4>
          <p>1：选取三个号位置和号码与开奖号码一致为一等奖，奖金为奖池奖金的一半。如果多注中奖，即平分奖池一半奖金。如：奖池奖金为100元，中奖一注，即50元，中奖2注，即每注25元，以此类推。</p>
          <p>2：选取三个号码中任意两个号码和位置与开奖号码一致为二等奖，二等奖固定奖金为200元。如：开奖号码为123，即二等奖号码有。103.113.133.143.153.163.173.183.193.023
            .223.323.423.523.623.723.823.923.120.121.122.124.125.126.127.128.129.</p>
          <h4>奖池说明：</h4>
          <p>1：购买彩票资金全部归入奖池。</p>
          <p>2：奖池50%资金为一等奖准备。剩余50%为二等奖和好友推荐佣金。</p>
          <p>3：开奖剩余资金全部归入下期奖池。</p>
          <h4>充值说明：</h4>
          <p>可以通过微信和支付宝扫描支付充值。</p>
          <h4>提现说明：</h4>
          <p>提现区留下资料，提现到银行卡里，每天10-16点为提现通过时间，其余时间可以申请（法定节假日，有可能会延迟，敬请谅解。</p>
        </div>

        <!-- 底部导航菜单 -->
        <tabbar :selected="selected"></tabbar>
    </div>
</template>

<script>
    import tabbar from './common/UserTabbar';
    export default {
        name: "ShoppingList",
        data(){
            return{
              selected:"ShoppingList",  //当前页的状态
            }
        },
        components:{
            tabbar
        },
    }

</script>
<style>
#playheight{color: #2d2d2d;background: #fff;}
.playRule{width: 94%;margin: 1rem auto 0 auto;padding-bottom: 2rem;}
.playRule h4{font-size: 14px;height: 36px;line-height: 36px;color: #ff414f;text-indent: 7px;font-weight: 500;}
.playRule p{font-size: 12px;margin: 0 0 0.2rem 6px;font-weight: 600;width: 97%;word-wrap: break-word;word-break: break-all;overflow: hidden;}
.tabTxt{font-size: 0.25rem;border: 1px solid #ccc;margin: 0 0 0.2rem 6px;width: 95%;text-align: center;border-collapse:collapse;}
.tabTxt tr{height: 30px;line-height: 30px;}
.tabTxt tr th{font-size: 14px;border: 1px solid #ccc;}
.tabTxt tr td{font-size: 12px; color: #2d2d2d;border: 1px solid #ccc;}
table th{background:#7c7c7c;font-weight:normal;line-height:30px;font-size:14px;color:#FFF;}
table tr:nth-child(odd){background:#F4F4F4;}
table td:nth-child(even){color:#fe515e;width: 46%;line-height: 20px;}


</style>



