<template>
  <div id="UserRecharge">
    <!-- 头部 -->
    <mt-header title="充值" id="rechargeHeader">
      <router-link to="/UserCenter" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="RechargeBox">
      <h4 class="RechargeTitle">请选择充值金额</h4>
      <!-- <div class="RechargeText"><label for="">￥</label><input type="number" v-model="mony" placeholder="请输入充值金额"></div> -->
      
      <ul class="monerBoxx">
        <li v-for="dat in data" :key="dat.dat" :class="['moneyList',{moneyActive:selectId==dat.id}] "  @click="selectIdFun(dat.id)" >
           <span>{{dat.cardName}}</span>
        </li>
      </ul>

      <div class="zgee">
        <h4 class="RechargeTitle">支付方式</h4>
        <div :class="['typeItem',{Fsactive:selectType==1}] " @click="selectType=1">
          <b class="ico-zf"><b></b></b>
          <img src="./../assets/images/icon_zhifubao.png" class="zfb-ico">
          <span class="zf-txt">支付宝<p>安全支付</p></span>
        </div>
        <div :class="['typeItem',{Fsactive:selectType==2}] " @click="selectType=2">
          <b class="ico-wx"><b></b></b>
          <img src="./../assets/images/icon_weixin.png" class="zfb-ico">
          <span class="zf-txt">微信<p>安全支付</p></span>
        </div>
      </div>

    </div>
    <form style='display:none;' id='formpay' name='formpay' method='post' action='https://pay.paysapi.com' >
        <input name='goodsname' id='goodsname' type='text' v-model="formData.goodsname" />
        <input name='istype' id='istype' type='text' v-model="formData.istype" />
        <input name='key' id='key' type='text' v-model="formData.key"/>
        <input name='notify_url' id='notify_url' type='text' v-model="formData.notify_url"/>
        <input name='orderid' id='orderid' type='text' v-model="formData.orderid"/>
        <input name='orderuid' id='orderuid' type='text' v-model="formData.orderuid"/>
        <input name='price' id='price' type='text' v-model="formData.price"/>
        <input name='return_url' id='return_url' type='text' v-model="formData.return_url"/>
        <input name='uid' id='uid' type='text' v-model="formData.uid"/>
        <input type='submit' id='submitdemo1'>
    </form>

    <div class="RechargeBtn"><mt-button type="danger" class="ToBtn" @click="ToBtn">下一步</mt-button></div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
    name: "UserRecharge",
    data(){
        return{
          data:[],
          categories:[],
          selectId:"",
          selectType:1,
          formData:{}
        }
    },
    created(){
      axios.post("/api/card/getAll").then((data)=>{
        //console.log(data)
        //console.log(data.data.data)
        this.data = data.data.data
        //console.log(this.data)
      })
    },
    methods : { 
      selectIdFun:function(id){
          this.selectId=id
      }, 
      ToBtn:function(){
        var that=this;
      //请求奖池金额private BigDecimal pricePond
      if(that.selectId){
        axios.post("/api/recharge/paysApiPay",{
            cardId:that.selectId,
            type:that.selectType
        }).then((data)=>{
            console.log(data)
            if(data.data.code == 20005){ 
                alert(data.data.msg)
            }else{
                that.formData=data.data.data
                setTimeout(function(){
                  document.getElementById("formpay").submit();
                },500)
            }  
        })
      }else{
        alert("请选择充值金额")
      }
        
      }
    },
  }
</script>

<style>
#rechargeHeader{color: #2d2d2d;background: #fff;}
.RechargeBox{background: #fff;margin-top: 15px;}
.RechargeTitle{font-size: 0.26rem;color: #4a4a4a;font-weight: 400;text-indent: 0.2rem;padding-top: 0.2rem;border: 1px solid #F4F4F4;padding-bottom: 0.2rem;}
.RechargeText{margin-top: 0.15rem;padding-bottom: 0.15rem;text-indent: 0.2rem;height: 0.8rem; line-height: 0.8rem; display: flex;}
.RechargeText label{font-size: 0.3rem; color: #363636;line-height: 0.75rem;}
.RechargeText input{height: 0.8rem;border: 0;width: 60%;font-size: 0.32rem;outline: none;text-indent: 0.2rem;}
.RechargeBtn{margin-top: 0.2rem;}
.ToBtn{width: 60%; margin-left: 20%;}
.moneyList{font-size: 0.25rem;}
.zgee{clear: both;border-top: 10px solid #f6f6f6;display: inline-block;width: 100%;}

.monerBoxx{display: inline-block;width: 100%;}
.monerBoxx .moneyList{font-size: 0.25rem;margin-left: 8%;height: 40px;line-height: 40px;width: 20%;text-align: center;display: inline-block;border: 1px solid #979797;border-radius: 4px;margin-top: 0.2rem; margin-bottom: 0.2rem;}
.monerBoxx .moneyList span:nth-child(2){text-align: center}
/*.monerBoxx .moneyList:nth-child(1){float: left;margin-left: 12%}*/
/*.monerBoxx .moneyList:nth-child(2){float: right;margin-right: 12%}*/
.moneyActive{border: 1px solid #3CA9E3 !important; color: #3CA9E3;}
.Fsactive .ico-wx{ border: 1px solid #52D380;}
.Fsactive .ico-wx b{background: #52D380}

.Fsactive .ico-zf{ border: 1px solid #009FE8;}
.Fsactive .ico-zf b{background: #009FE8;}


.typeItem{width: 100%;height: 50px;float: left;font-size: 14px;border-bottom: 1px solid #F4F4F4;}
.ico-zf{display:inline-block; width: 10px;height: 10px; margin: 12px 14px 0 10px; background: #fff;border: 1px solid #979797; vertical-align: middle; border-radius: 100%; padding: 3px;}
.ico-zf b{display: block;width: 10px; height: 10px; background: #fff; border-radius: 100%;}
.zfb-ico{display: inline-block;width: 30px;height: 30px;vertical-align: middle;margin: 10px 10px 0 0;}

.ico-wx{display:inline-block; width: 10px;height: 10px; margin: 12px 14px 0 10px; background: #fff;border: 1px solid #979797; vertical-align: middle; border-radius: 100%; padding: 3px;}
.ico-wx b{display: block;width: 10px; height: 10px; background: #fff; border-radius: 100%;}
.zf-txt{display: inline-block;vertical-align: middle;margin: 10px 0 0 0;font-size: 0.25rem;color: #4A4A4A;font-weight: 600;}
.zf-txt p{font-size: 0.2rem;font-weight: 300;}
</style>



