<template>
  <div id="Home">
    <!-- 头部 -->
    <mt-header fixed title="温州快8"></mt-header>
    <!-- 底部导航菜单 -->
    <tabbar :selected="selected"></tabbar>
    <!-- 奖池累计金额 -->
    <div class="PrizePoolCont">
      <p class="PrizePoolTxt">当前期数奖池累计金额</p>
      <h3 class="PrizePoolMoney" >{{PrizePool}}</h3>
    </div>
    <!-- 待开奖 -->
    <div class="nextPeriods">
      <div class="race" v-if="minute=='00'&&second<=30">正在开奖，停止投注</div>
      <div class="race" v-else>待开奖</div>
      <span class="periodsTime">{{minute}}:{{second}}</span>
      <div class="openingTime">距<span class="periodsTxtNumber">{{NextPeriods}}</span>期截止有</div>
    </div>
    <!-- 上期开奖 -->
    <div class="periodsCont">
      <div class="periodsTxt"><img src="./../assets/images/cpicon.png" alt="最新彩票"></div>
      <div class="beforePeriods">第<span>{{periods}}</span>期</div>
      <ul class="periodsNmb">
        <li v-for="PrizeNb in PrizeNumber" v-bind:key="PrizeNb.value">{{PrizeNb}}</li>
      </ul>
      <div class="jxt">
        <span>一等奖&nbsp;&nbsp;&nbsp;{{oneet}}人&nbsp;{{oneMonet}}元</span>&nbsp;&nbsp;&nbsp;&nbsp;
        <span>二等奖&nbsp;&nbsp;&nbsp;{{twomrt}}人&nbsp;{{twoMonet}}元</span>
      </div>
    </div>
    <!-- 中奖榜单 -->
    <div class="bdFt">
      <h4 class="listTitle">最新中奖榜</h4>
      <div class="itemList">
        <ul id="con1" ref="con1" :class="{anim:animate==true}">
          <li v-for='item in list' v-bind:key="item.value">
            <span>{{item.phone}}</span>
            <span>喜中{{item.winMoney}}元</span>
            <span>{{item.winLevel}}等奖</span>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
import tabbar from './common/UserTabbar';
import axios from 'axios';
export default {
    name: "Home",
    data(){
        return{
            selected:"MainPage",  //当前页的状态
            PrizePool:"",  //奖池金额
            periods:"",  //当前期数
            PrizeNumber:[], //当前奖池数
            NextPeriods:"", //下一个期数
            list:[],  //奖池列表内容
            animate:false,  //奖池列表

            minutes:0, //倒计时-分
            seconds: 0,   //倒计时-秒
            oneMonet:'',// 一等奖多少元
            twoMonet:'',// 二等奖多少元

            oneet:0, //一等奖多少人
            twomrt:0, //二等奖多少人


            //time:null
        }
    },
    components:{
      tabbar   
    },
    created(){

      //请求奖池金额private BigDecimal pricePond
      axios.post("/api/user/userIndexInfo.dnl")
      .then((data)=>{
        console.log(data);
        //return data.json
        this.PrizePool = data.data.data.pricePond;
        this.NextPeriods = data.data.data.phase;
        this.periods = data.data.data.lastPhase.phase;
        this.PrizeNumber = data.data.data.lastPhase.winingNumber;
        this.list = data.data.data.pricePeopleList;
        this.oneMonet = data.data.data.lastPhase.firstPrice;
        this.twoMonet = data.data.data.lastPhase.secondPrice;
        // let oneMonet=0;let twoMonet=0;
        for(var i=0;i<data.data.data.pricePeopleList.length;i++){
          var item=data.data.data.pricePeopleList[i];
          console.log(item)
          if(item.winLevel=='1'){
              this.oneet++;
          }
          if(item.winLevel=='2'){
              this.twomrt++;
          }
        }
        this.formatDuring(data.data.data.countDown);


        //console.log(data.data.data.lastPhase.firstPrice)
        //this.PrizePool = item.data.slice(0,1)
      });

      //手机号用***隐层
      var shuju = this.list
      //console.log(shuju)
      for(var k in shuju){
        //console.log(shuju[k].name.substr(0, 3)+ '****'+ shuju[k].name.substr(7));
        shuju[k].name = shuju[k].name.substr(0, 3)+ '****'+ shuju[k].name.substr(7)
        //console.log(shuju[k].name)
      }

      this.add()
      //获奖列表调用
      setInterval(this.scroll,2000)
    },

    mounted:function () {
        var that=this;
        console.log("测试");
        var socket = new SockJS('/wsapi/webServer');
        var stompClient = Stomp.over(socket);
        stompClient.connect({}, onConnected);
        function onConnected() {
          console.log("获取到了")
          stompClient.subscribe('/topic/index/public', function (message) {
            console.log("112321321");
            console.log(message.body);
            var data=JSON.parse(message.body)
            // data = parseJSON(message.body);
            that.PrizePool =data.pricePond;
            that.NextPeriods =data.phase;
            that.periods =data.lastPhase.phase;
            that.PrizeNumber =data.lastPhase.winingNumber;
            that.list =data.pricePeopleList;
            that.oneMonet = data.firstPrice;
            that.twoMonet = data.secondPrice;
            that.formatDuring(data.countDown);

          });
        }
    },
    methods: {
     formatDuring(mss){
         var days = parseInt(mss / (1000 * 60 * 60 * 24));
      var hours = parseInt((mss % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      var minutes = parseInt((mss % (1000 * 60 * 60)) / (1000 * 60));
      var seconds = (mss % (1000 * 60)) / 1000;
      this.minutes=minutes;
      this.seconds=parseInt(seconds)
      console.log(days + " 天 " + hours + " 小时 " + minutes + " 分钟 " + seconds + " 秒 ");


     },
      //获奖列表无缝滚动
      scroll(){
        this.animate=true;  //因为在消息向上滚动的时候需要添加css3过渡动画，所以这里需要设置true
        setTimeout(()=>{  //这里直接使用了es6的箭头函数，省去了处理this指向偏移问题，代码也比之前简化了很多
                this.list.push(this.list[0]); //将数组的第一个元素添加到数组的
                this.list.shift();  //删除数组的第一个元素
                this.animate=false; //margin-top为0 的时候取消过渡动画，实现无缝滚动
        },1000)
      },

      //开奖倒计时
      num: function (n) {
        return n < 10 ? '0' + n : '' + n
      },
      add: function () {
        var _this = this
        var time = window.setInterval(function () {
          if (_this.seconds === 0 && _this.minutes !== 0) {
            _this.seconds = 59
            _this.minutes -= 1
          } else if (_this.minutes === 0 && _this.seconds === 0) {
            _this.seconds = 0
            window.clearInterval(time)
          } else {
            _this.seconds -= 1
          }
        }, 1000)
      }

    },
     watch: {
       second: {
         handler (newVal) {
           this.num(newVal)
         }
       },
       minute: {
         handler (newVal) {
           this.num(newVal)
         }
       }
     },
    computed: {
      second: function () {
        return this.num(this.seconds)
      },
      minute: function () {
        return this.num(this.minutes)
      }
    }
  }
</script>

<style>
  #indeG{background: #fff !important;}
  .mint-header{background: #fff !important; color: #2d2d2d !important;font-weight: bold;}
  .PrizePoolCont{height:3rem;margin-bottom: 0.9rem; background: url(./../assets/images/PrizePool1.jpg) no-repeat;position: relative; top: 40px; background-size: 100%;}
  .PrizePoolTxt{font-size: 0.25rem;color: #fff;padding-top: 0.45rem;padding-left: 0.45rem;}
  .PrizePoolMoney{font-size: 0.8rem;margin-left: 0.45rem;margin-top: 0.3rem;color: #fff;font-weight: 400;}
  .mint-tabbar{background: #fff;box-shadow: 0px 1px 7px #e1e0e0;}
  .periodsCont{font-size: 0.2rem;padding-top: 0.18rem;background: #fff;margin-top: 0.2rem;padding: 0.4rem 0;}
  .periodsTxt{display: inline-block;margin-left: 0.3rem;}
  .periodsTxt img{width: 0.7rem; height: 0.7rem;vertical-align: middle;}
  .beforePeriods{display: inline-block;font-weight: 600;color: #595d5e;line-height: 0.65rem;vertical-align: middle;margin-left: 1.6rem;font-size: 0.3rem;height: 0.65rem;}
  .beforePeriods span{color: #ff414f;padding: 0 0.15rem;}
  .periodsNmb{display: inline-block;margin-top: 0.4rem;width: 100%;text-align: center;}
  .periodsNmb li{display: inline-block;border: 1px solid #c6c4c4;border-radius: 100%;height: 0.8rem;width: 0.8rem;text-align: center;line-height: 0.8rem;color: #595d5e;font-size: 0.35rem;margin-left: 0.48rem;}
  .periodsNmb li:nth-child(1){margin-left: 0;}
  .nextPeriods{font-size: 0.25rem;color: #595d5e;font-weight: 600;background: #fff;padding: 0.4rem 0;}
  .nextPeriods .race{text-align: center;margin-bottom: 0.55rem;color: #939393;font-size: 0.3rem;}
  .nextPeriods .periodsTxtNumber{color: #ff414f;padding: 0 0.15rem;}
  .periodsTime{float: right;padding-right: 0.3rem;}
  .openingTime{text-indent: 0.3rem;}
  .itemList{display: inline-block;width: 100%;height: 4rem;overflow: hidden;margin-top: 0.3rem;margin-bottom: 60px;}
  .anim{transition: all 0.5s;margin-top: -30px;}
  #con1 li{list-style: none;line-height: 30px;height: 30px;font-size: 0.25rem;}
  #con1 li span{display: inline-block; width: 32%;text-align: center;}
  .listTitle{border-bottom: 2px solid #f6f6f6;font-size: 0.3rem;font-weight: 400;text-indent: 0.35rem;margin-top: 0.2rem;height: 0.84rem;line-height: 0.84rem;color: #ff414f;}
  .jxt{text-align: center;color: #FF414F;font-size: 0.25rem;margin-top: 0.4rem;}
  .bdFt{background: #fff;}
</style>



