<template>
  <div id="zhongj">
    <mt-header title="中奖列表" id="zhongjBj">
        <router-link to="/UserCenter" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      
      </mt-header>
      <div class="zhongjiangList" v-for="(item,i) in list" :key='i'>
            <i class="ic"></i>
            <b class="zjTime">{{item.insertTimeStr}}</b>
            <b class="ykj">{{show(item.status)}}</b> 
            <p class="bse">
              &nbsp;倍&nbsp;数：<b>{{item.multiple}}</b>
              &nbsp;期&nbsp;数：<b>{{item.phase}}</b>
              &nbsp;下单号：<b>{{item.lotterNumber}}</b>
            </p>
            <p class="mnr" v-if='item.winLevel'><b>{{item.winLevel}}</b>等奖<b>{{item.winMoney}}</b>元</p>
            <p class="mnr" v-else>未中奖</p>  
      </div>     
      
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    name: "zhongj",
    data(){
      return{
        list:[],
        page:{
            size:10,
            current:1
        }
      }
    },
    created(){
      if(this.$store.state.token){
        this.$router.push('/zhongjiang');
      }else{
        this.$router.push('/UserLogin');
      }
      axios.post("/api/order/listOrderByUser",{page:this.page}).then((data)=>{
            console.log(data.data)

              if(data.data.code=="10000"){
              this.list=data.data.data.records

             }
        })
    },
    methods:{
      show:function(status){
        if(status=='LOTTER_OPEN'){
            return '已开奖'
        }else{
            return '未开奖'
        }
      },
      
    }
  }
</script>

<style>
*{font-size: 0.25rem;}
#zhongjBj{color: #2d2d2d;background: #fff;}
.zhongjiangList{width: 96%; margin-top: 8px; background: #fff; padding: 0.3rem 2%;}
.zhongjiangList .ic{display: inline-block; width: 20px; height: 20px;  background: url("./../assets/images/icon_tixian@3x.png")no-repeat; background-size: 100%;    vertical-align: middle;margin: 10px 8px 0 6px;}
.zjTime{display: inline-block;font-size: 12px;vertical-align: middle;padding-top: 0.2rem;color: #666;}
.ykj{display: block;margin-top: 10px;float: right;padding-right: 0.2rem;}
.bse{display: block;padding-left: 37px;padding-top: 0.1rem;}
.bse b{color: red;}
.mnr{display: block;padding-left: 37px;padding-top: 0.1rem;}
.mnr b{color: red;margin:0 0.2rem;}
</style>



