<template>
  <div id="UserCenter">
    <!-- 底部导航菜单 -->
    <tabbar :selected="selected"></tabbar>

    <!-- 头部 -->
    <header class="UserHerder">
        <h5 class="UserHerderTopTxt">我的</h5>
        <div class="HerderCont">
          <div class="userImages"><img src="./../assets/images/userImg.png"></div>
          <router-link to="/UserLogin" class="nserName"><span>{{userInfo.nickName}}</span></router-link>
          <div class="UserBalance">
            <div>
              <p>余额</p>
              <span>{{userInfo.balance}}</span>
            </div>
            <div></div>
            <div>
              <p>佣金</p>
              <span>{{userInfo.countPriceMoney}}</span>
            </div>
          </div>
        </div>
    </header>

    <div class="notesList">
      <mt-cell title="充值" is-link to="/UserRecharge" class="listText">
        <img slot="icon" src="./../assets/images/ico_cz.png" class="listIcon">
      </mt-cell>
      <mt-cell title="提现列表" is-link to="/UserEmbodyList" class="listText">
        <img slot="icon" src="./../assets/images/ico_tx.png" class="listIcon">
      </mt-cell>

      <mt-cell title="充值记录" is-link to="/chongzhi" class="listText">
        <img slot="icon" src="./../assets/images/czjl.png" class="listIcon">
      </mt-cell>
      <mt-cell title="中奖列表" is-link to="/zhongjiang" class="listText">
        <img slot="icon" src="./../assets/images/zj.png" class="listIcon">
      </mt-cell>


      <mt-cell title="个人信息" is-link to="/PersonalInformation" class="listText">
        <img slot="icon" src="./../assets/images/icoinf.png" class="listIcon">
      </mt-cell>
      <mt-cell title="修改密码" is-link to="/UpdatePwd" class="listText">
        <img slot="icon" src="./../assets/images/mnxf.png" class="listIcon">
      </mt-cell>

      <div @click="copeUrl()" class="yq-ple" :data-clipboard-text="copytarget">
        <mt-cell title="邀请好友"   class="listText">
          <img slot="icon" src="./../assets/images/icon_yaoqing@2x.png" class="listIcon">
        </mt-cell>
      </div>

      <mt-cell title="客服" is-link to="/CustomService" class="listText">
        <img slot="icon" src="./../assets/images/icon_kefu @2x.png" class="listIcon">
      </mt-cell>
      <!-- <mt-cell title="关于我们" is-link to="/AboutIntroduce" class="listText">
        <img slot="icon" src="./../assets/images/icon_guanyu @2x.png" class="listIcon">
      </mt-cell> -->



      <div  @click="outLogin" class="wqr">
      <mt-cell title="退出登录" class="logOff"></mt-cell>
      </div>
    </div>
    <div class="bj"></div>
  </div>
</template>

<script>
import tabbar from './common/UserTabbar';
import axios from 'axios';
import Clipboard from 'clipboard';
export default {
    name: "UserCenter",
    data(){
        return{
            selected:"UserCenter",  //当前页的状态
            userInfo:{
              nickName:"",
              countPriceMoney:"",
              balance:""
            },
            copytarget: ''
        }
    },
    created(){
      var that=this;
      //请求奖池金额private BigDecimal pricePond
      axios.post("/api/user/getUserPersonal")
      .then((data)=>{
         console.log(data.data);
         if(data.data.code=="10000"){
            that.userInfo=data.data.data;
             that.$store.commit('changeuserinfo',data.data.data)
             localStorage.setItem('userInfo',JSON.stringify(data.data.data));
         }
        //return data.json

        //this.PrizePool = item.data.slice(0,1)
      })


    },
    components:{
      tabbar
    },methods : {
      copeUrl:function(){
          // var domain = window.location.host;
          // var Url2 = "http://"+domain+'/UserRegister?parentId='+this.userInfo.userNumber;
          // // var Url2 = domain+'/UserRegister?parentId='+this.userInfo.userNumber;
          // var oInput = document.createElement('input');
          // oInput.value = Url2;
          // document.body.appendChild(oInput);
          // oInput.select();
          // document.execCommand("Copy");
          // oInput.className = 'oInput';
          // oInput.style.display = 'none';
          // alert('复制分享链接成功，请前往微信朋友圈进行分享！');
        this.doCopy()
      },
      outLogin:function () {
        console.log("11")
        this.$store.commit('removetoken')
        this.$router.push('/');
      },
      doCopy () {
        var domain = window.location.host;
        var Url2 = "http://"+domain+'/UserRegister?parentId='+this.userInfo.userNumber;
        this.copytarget = Url2
        var clipboard = new Clipboard('.yq-ple')
        clipboard.on('success', e => {
          alert('复制分享链接成功，请前往微信朋友圈进行分享！');
          // 释放内存
          clipboard.destroy()
        })
        clipboard.on('error', e => {
          // 不支持复制
          console.log('该浏览器不支持自动复制')
          // 释放内存
          clipboard.destroy()
        })
      }
    }
  }
</script>

<style>


  /* 头部样式 */
  .UserHerder{height: 3.6rem;background:url(./../assets/images/my-bj.png) no-repeat; background-size: 100%; padding-top: 0.4rem;text-align: center;}
  .UserHerderTopTxt{color: #fff;font-size: 0.3rem; margin-bottom: 0.6rem;}
  .HerderCont{width: 94%;height: 3.4rem;background: url(./../assets/images/myUser.png)no-repeat;background-size: 100%;margin: 1rem auto 0 auto;position: relative;z-index: 99;}
  .userImages{width: 1.3rem; height: 1.3rem;overflow: hidden; border-radius: 100%;border:2px solid #fff;position: absolute;left: 50%; margin-left: -0.65rem;top: -0.5rem;}
  .userImages img{width: 100%;}
  .nserName{color: #fff; font-size: 0.25rem;display: inline-block; margin-top: 1.2rem;}
  .UserBalance{font-size: 0.25rem; width: 80%;margin: 0.25rem auto 0 auto;height: 0.8rem;}
  .UserBalance div{display: inline-block; font-size: 0.25rem; color: #fff;}
  .UserBalance div:nth-child(2){margin: 0 0.62rem;border-right: 1px solid #fff;height: 0.5rem; margin-top: 0.15rem}

  /* 列表样式 */
  .notesList{margin-top: 1rem;margin-bottom: 1.5rem;}
  .listIcon{width: 18px; height: 18px;margin-right: 10px;}
  .listText{color: #4a4a4a;}
  .mint-cell-allow-right::after {border: solid 1px #c8c8cd;border-bottom-width: 0;border-left-width: 0;width: 6px;height: 6px;}
  .yq-ple{border-top: 10px solid #f6f6f6;}
  .mint-cell:nth-child(7){border-bottom: 10px solid #f6f6f6;}
  .mint-cell-wrapper {font-size: 0.25rem;background-image: -webkit-gradient(linear, left top, left bottom, from(#f6f6f6), color-stop(50%, #f6f6f6), color-stop(50%, transparent));background-image: linear-gradient(180deg, #f6f6f6, #f6f6f6 50%, transparent 50%);padding: 0 0px;width: 100%; text-indent: 10px;}
  .logOff{margin-bottom: 1.1rem;border-bottom: 10px solid #f6f6f6; text-align: center}
  .wqr{margin-bottom: 30px;}
</style>



