<template>

    <div id="PersonaInfo">
      <mt-header title="修改密码" id="Pwd">
        <router-link to="/UserCenter" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
        <div class="topb">
            <input type="password" class="psdtxt"  v-model="info.password"  placeholder="请输入新密码">
            <input type="password" class="psdtxt"  v-model="info.qrpassword"  placeholder="请确认新密码">
            <input type="password" class="psdtxt"  v-model="info.payPassword"  placeholder="请输入支付密码">
        </div>
        <button @click="endgBtn" class="xiugai">确认修改</button>
    </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "PersonaInfo",
  data(){
    return{
        info:{
            payPassword:"",
            qrpassword:"",
            password:""
        },
        tipTxt:""
    }
    
  },
  created(){
     
        this.info.password=this.$store.state.userInfo.password
        this.info.payPassword=this.$store.state.userInfo.payPassword

    },
  methods: {

    endgBtn:function(){
        var that=this;
         if(this.info.password == ""){
            this.tipTxt="请输入新密码";
            this.tipBox();
            return false;
        }
        if(this.info.password != this.info.qrpassword){
          this.tipTxt="两次密码输入不一致";
          this.tipBox();
          return false;
        }
        if(this.info.payPassword == ""){
          this.tipTxt="支付密码不能为空";
          this.tipBox();
          return false;
        }
         axios.post("/api/user/updateUser",this.info).then((data)=>{
            console.log(data)
            alert("修改成功")
        })
        
       
    },
    tipBox:function(){
        alert(this.tipTxt);
        //执行封装的报错弹窗提醒，错误提示文本为this.tipTxt
    },
    
    
  }
}
</script>

<style>
*{font-size: 0.25rem;}
#Pwd{color: #2d2d2d;background: #fff;}
.topb{background: #fff;padding-bottom: 0.3rem;margin-top: 0.2rem;}
.psdtxt{background: #F7F4F8;outline: none;border: none;color: #b7b5b5;width: 60%;margin-top: 15px;margin-left: 20%;height: 40px;border-radius: 6px;text-indent: 8px;}
.xiugai{display: inline-block;line-height: 1;white-space: nowrap;cursor: pointer;background: #fff;border: 1px solid #dcdfe6;color: #606266;-webkit-appearance: none;text-align: center;-webkit-box-sizing: border-box;box-sizing: border-box;outline: 0;margin: 0;-webkit-transition: .1s;transition: .1s;font-weight: 500;padding: 12px 20px;font-size: 14px;border-radius: 4px;background: #FF414F;width: 60%;color: #fff;margin-top: 30px;margin-left: 20%;}
</style>



