<template>
  <div id="chongzhi">
    <mt-header title="充值列表" id="chongzhiBj">
        <router-link to="/UserCenter" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>


    <div class="chongzhiList" v-for="(item,i) in list" :key='i'>
      <p>
        <b class="icotb"></b>
        <span class="chongzhiTime">{{item.insertTimeStr}}</span>
        <b class="chongz-tx">{{show(item.status)}}</b>
      </p>
      <span class="chongmn">{{item.money}}元</span>
    </div>    
    
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    name: "chongzhi",
    data(){
      return{
        list:[],
        page:{
            size:10,
            current:1
        }
      }
    },
    created(){
      axios.post("/api/recharge/listUserRechagre",{page:this.page}).then((data)=>{
            console.log(data.data)
             if(data.data.code=="10000"){
             this.list=data.data.data.records

            }
        })
    },
    methods:{
      show:function(status){
        if(status=='APPLY_ING'){
            return '充值失败'
        }else{
            return '充值成功'
        }
    },
    }
  }
</script>

<style>
*{font-size: 0.25rem;}
#chongzhiBj{color: #2d2d2d;background: #fff;}
.chongzhiList{width: 96%; margin-top: 8px; background: #fff; padding: 0.3rem 2%; }
.chongzhiList .icotb{display: inline-block; width: 20px; height: 20px;  background: url("./../assets/images/icon_tixian@3x.png")no-repeat; background-size: 100%;    vertical-align: middle;margin: 10px 8px 0 6px;}
.chongzhiTime{display: inline-block;font-size: 12px;vertical-align: middle;padding-top: 0.2rem;}
.chongz-tx{display: inline-block;vertical-align: middle;margin-top: 10px;float: right;padding-right: 0.2rem;}
.chongmn{display: block;color: red;padding-left: 37px;padding-top: 0.2rem;}
</style>



