<template>
    <div id="UserEmbody">
        <!--<mt-header title="提现列表" id="embodyHeader">-->
            <!--<router-link to="/UserCenter" slot="left">-->
                <!--<mt-button icon="back"></mt-button>-->
            <!--</router-link>-->
        <!--</mt-header>-->

      <mt-header title="提现列表" id="embodyHeader">
        <router-link to="/UserCenter" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
        <mt-button  slot="right">
          <mt-button type="danger" class="bodyBtn" id="btntx" @click="goTx">提现</mt-button>
        </mt-button>
      </mt-header>

        <div class="txList">
            <div class="itembh itemBox" v-for="(item,i) in list" :key='i'>
                <b class="icot"></b>
                <!--{{i+1}} -->
                <div class='status'>{{show(item.status)}}</div>
                <br>
                <p class="timtxt">{{item.insertTimeStr}}</p>
                <span class="moneyLr">{{item.money}}元</span>

            </div>    
        </div>    
        <!--<div class="embodyBtn">-->
        <!--&lt;!&ndash;<mt-button type="danger" class="bodyBtn" @click="returnPage">返回</mt-button>&ndash;&gt;-->
        <!--</div>-->

        

    </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "UserEmbodyList",
  data(){
    return{
      money:"", //体现金额
      usermy:0, //可用体现金额
      bankflag:0,
      list:[],
      page:{
          size:10,
          current:1
      }
    }
    
  },
  created(){
        axios.post("/api/apply/userApplyList",{page:this.page}).then((data)=>{
            console.log(data.data)
            if(data.data.code=="10000"){
            this.list=data.data.data.records

            }
        })
    },
  methods: {

    show:function(status){
        if(status=='APPLY_ING'){
            return '提现申请中'
        }else if(status=='APPLY_WAITTING_MONEY'){
            return '等待打款'
        }else if(status=='APPLY_SUCCESS'){
            return '提现成功'
        }else if(status=='APPLY_REFUSE'){
            return '提现拒绝'
        }else{
            return '提款异常'
        }
    },
       goTx:function(){
            this.$router.push('/UserEmbody');  
      },
      returnPage:function(){
            this.$router.push('/UserCenter');  
      },
    endgBtn:function(){
        var money =  this.money;
        if(money == ""){
            this.tipTxt="请输入提现金额";
            this.tipBox();
            this.bankflag=0;
            return false;
        }else if(money > this.usermy){
            this.tipTxt="提现金额大于余额";
            this.tipBox();
            this.bankflag=0;
            return false;
        }
       
       
    },
    tipBox:function(){
        alert(this.tipTxt);
        //执行封装的报错弹窗提醒，错误提示文本为this.tipTxt
    },
    
    
  }
}
</script>

<style>
*{font-size: 0.25rem;}
#embodyHeader{color: #2d2d2d;background: #fff;}
.mint-field .mint-cell-title{width: 50px}
.embodyTop{display:block; margin-top: 20px;}
.mint-cell-wrapper {font-size: 0.25rem;background-image: -webkit-gradient(linear, left top, left bottom, from(#f6f6f6), color-stop(50%, #f6f6f6), color-stop(50%, transparent));background-image: linear-gradient(180deg, #f6f6f6, #f6f6f6 50%, transparent 50%);padding: 0 0px;width: 100%; text-indent: 10px;}
.mint-cell:nth-child(4){margin-top: 10px;}
.userMony{width: 100%;height: 48px;background: #fdfdfd;line-height: 48px;text-indent: 10px;color: #717171;}
.userMony span{padding-left: 0.2rem;}
.embodyBtn{border-top:4px solid #f6f6f6; margin-top: 0.4rem;}
.bodyBtn{width: 60%; margin-left: 20%;}
.bankList{width: 100%;height: 48px;border: 0;text-indent: 10px;outline: none;}
.mintui{margin-right: 0.2rem;}
.itembh{background: #fff; border-bottom: 1px solid #F4F4F4;padding: 0.3rem 0;}
.txList{border-top: 1px solid #f6f6f6}


.itemBox{position: relative}
.icot{display: inline-block; width: 20px; height: 20px;  background: url("./../assets/images/icon_tixian@3x.png")no-repeat; background-size: 100%;    vertical-align: middle;margin: 0 8px 0 10px;}
.status{display: inline-block;color: #4A4A4A; font-weight: 600;}
.timtxt{color: #B8B8B8; text-indent: 42px; font-size: 0.2rem;}
.moneyLr{position: absolute;right: 15px;top: 22px;font-weight: 600;color: #ff414f !important;}
#btntx{width: 50px !important;color: #ff414f !important;}
#btntx  label{font-size: 0.3rem;}
</style>



